<template>
  <div class="map-box">
    <div id="viewDiv" ref="viewDiv"></div>
    <!-- 图例 -->
    <div id="legendbar">
      <span>图例</span>
      <Legend :data="legendData" id="legend"></Legend>
    </div>
    <Spin size="large" fix v-if="spinShow"></Spin>
  </div>
</template>

<script>
import arcgisPackage from '../../../utils/arcgisTools'
import config from '../../../setting/mapConfig'
import { tdtlayer, tdtyNoteslayer, tdtclayer, tdtcNoteslayer, redLine, shengjie } from '@/utils/layers'
import { cacuDMS } from '@/utils/common'
import Basemapthumbnail from '../../../assets/map_images/Basemapthumbnail.png'
import Basemapthumbnail1 from '../../../assets/map_images/Basemapthumbnail1.png'
import Legend from '@/components/Legend'
// 离线图标
import patrolUserGray from '../../../assets/boundaryMarker_images/guai_point3.png'
// 在线图标
import patrolUserRed from '../../../assets/boundaryMarker_images/preset_bm3.png'
// 巡查中图标
import patrolUserBlue from '../../../assets/boundaryMarker_images/preset_sign3.png'
import {
  // 实时位置
  listPhoneUserToView
} from '@/api/workRegulation'
export default {
  name: 'workRegulation',
  data () {
    return {
      legendData: [
        {
          type: 1,
          name: '离线',
          background: 'url(' + patrolUserGray + ')',
          id: 'patrolUserGray'
        },
        {
          type: 1,
          name: '在线',
          background: 'url(' + patrolUserRed + ')',
          id: 'patrolUserRed'
        }
        // {
        //   type: 1,
        //   name: '巡查中',
        //   background: 'url(' + patrolUserBlue + ')',
        //   id: 'patrolUserBlue'
        // }
      ],
      popupTemplate: {}, // 点击地图中点的弹框对象
      loadComplete: false, // 地图加载完成状态
      spinShow: true
    }
  },
  components: {
    Legend
  },
  mounted () {
    // 加载地图
    this.createMap()
    // 获取实时位置数据
    this.getLedgerList()
  },
  methods: {
    // 获取实时位置数据
    async getLedgerList () {
      await listPhoneUserToView({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          // 添加巡查人员
          this.addPointToMap(res.data.data.data, config.inflectionPointcolor, 'graphicsLayer_patroluser')
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /**
     * @Description:初始化地图的函数
     * @author 张淼
     * @date 2019/5/5
     */
    async createMap () {
      // 加载arcgis地图的依赖
      let MapView = await arcgisPackage.MapView
      let Map = await arcgisPackage.Map
      let Basemap = await arcgisPackage.Basemap
      let BasemapToggle = await arcgisPackage.BasemapToggle
      // 天地图影像地图
      let tdtylayer = await tdtlayer().then(res => { return res })
      // 天地图矢量地图
      let tdtylayer1 = await tdtclayer().then(res => { return res })
      // 天地图的矢量标注图层
      let tdtylayer2 = await tdtcNoteslayer().then(res => { return res })
      // 天地图影像标注图层
      let tdtylayer3 = await tdtyNoteslayer().then(res => { return res })
      // 省界
      let layerProvince = await shengjie().then(res => { return res })
      let customBasemap = new Basemap({
        baseLayers: [tdtylayer1, layerProvince, tdtylayer2],
        title: '矢量地图',
        id: 'myBasemap',
        thumbnailUrl: Basemapthumbnail
      })
      let customBasemap1 = new Basemap({
        baseLayers: [tdtylayer, layerProvince, tdtylayer3],
        title: '影像地图',
        id: 'myBasemap1',
        thumbnailUrl: Basemapthumbnail1
      })
      this.map = new Map({
        basemap: customBasemap1
      })
      this.view = new MapView({
        map: this.map,
        container: 'viewDiv',
        center: config.centerPoint,
        zoom: 7
      })
      this.view.when(wes => {
        this.loadComplete = true
      })
      // 弹框内容
      this.popupTemplate = {
        title: '巡查人员详情',
        content: [{
          type: 'fields',
          fieldInfos: [{
            fieldName: '用户名'
          },
          {
            fieldName: '手机号'
          },
          {
            fieldName: '真实姓名'
          },
          {
            fieldName: '经度'
          },
          {
            fieldName: '纬度'
          },
          {
            fieldName: '邮箱'
          }
          ]
        }]
      }
      // 增加底图切换插件
      let basemapToggle = new BasemapToggle({
        view: this.view,
        nextBasemap: customBasemap
      })
      this.view.ui.add(basemapToggle, 'top-right')
      // 添加图例
      this.view.ui.add(document.getElementById('legendbar'), 'bottom-left')
    },
    /**
     * 添加添加巡查人员
     * @param pointArray 点数组
     * @param symbolColor 符号颜色
     * @param layerId 自定义图层id
     */
    async addPointToMap (pointArray, symbolColor, layerId) {
      let GraphicsLayer = await arcgisPackage.GraphicsLayer
      let Graphic = await arcgisPackage.Graphic
      let layer = this.map.findLayerById(layerId)
      let point = {}
      let pointGraphic
      let textGraphic
      let wayPoint // 不同巡查状态图标
      let status // 巡查状态
      let lon // 经度
      let lat // 纬度
      let features = []
      if (layer) {
        this.map.remove(layer)
      }
      let graphicsLayer = new GraphicsLayer({
        id: layerId,
        elevationInfo: 'on-the-ground',
        title: '实时位置'
      })
      if (pointArray && pointArray.length > 0) {
        for (let i = 0; i < pointArray.length; i++) {
          if (pointArray[i].CH003 === null) {
            continue
          } else if (pointArray[i].CH003.indexOf('°') !== -1) { // 度分秒转为经纬度
            lon = cacuDMS(pointArray[i].CH003)
            lat = cacuDMS(pointArray[i].CH004)
          } else if (pointArray[i].CH003.indexOf('.') !== -1) {
            lon = pointArray[i].CH003
            lat = pointArray[i].CH004
          }
          point = {
            type: 'point', // autocasts as new Point()
            longitude: lon,
            latitude: lat
          }
          if (pointArray[i].CH005 === 0) { // 离线
            wayPoint = patrolUserGray
            status = '离线'
          } else if (pointArray[i].CH005 === 1) { // 在线
            wayPoint = patrolUserRed
            status = '在线'
          }
          // else if (pointArray[i].CH005 === 2) { // 巡查中
          //   wayPoint = patrolUserBlue
          //   status = '巡查中'
          // }
          // 属性信息
          let lineAtt = {
            'id': pointArray[i].SA001,
            '用户名': pointArray[i].SA008,
            '手机号': '<a style="color: blue">' + pointArray[i].SA012 + '</a>',
            '真实姓名': pointArray[i].SA019,
            '经度': lon,
            '纬度': lat,
            '邮箱': pointArray[i].SA013,
            '状态': status
          }
          if (pointArray[i].CH005 === 0) {
            lineAtt['手机号'] = '<a style="color: blue">' + pointArray[i].SA012 + '</a>'
          }
          let markerSymbol = {
            type: 'picture-marker', // autocasts as new SimpleMarkerSymbol()
            url: wayPoint,
            width: 20,
            height: 20,
            outline: { // autocasts as new SimpleLineSymbol()
              color: [255, 255, 255],
              width: 1
            }
          }
          let textSymbol = {
            type: 'text', // autocasts as new TextSymbol()
            color: 'white',
            haloColor: 'black',
            haloSize: '1px',
            text: pointArray[i].SA019,
            xoffset: 0,
            yoffset: -20,
            font: { // autocast as new Font()
              size: 12,
              family: 'sans-serif',
              weight: 'bold'
            }
          }
          // 构造点对象
          pointGraphic = new Graphic({
            geometry: point,
            symbol: markerSymbol,
            attributes: lineAtt,
            popupTemplate: this.popupTemplate
          })
          textGraphic = new Graphic({
            geometry: point,
            symbol: textSymbol
          })
          features.push(pointGraphic)
          features.push(textGraphic)
          graphicsLayer.add(pointGraphic)
          graphicsLayer.add(textGraphic)
        }
      }
      // 如果问题点对象图层存在，则将新点位添加到该对象
      this.spinShow = false
      this.map.add(graphicsLayer)
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  .map-box{
    width: 100%;
    height: 100%;
    position: relative;
    /* 地图框样式 */
    #viewDiv{
      width: 100%;
      height: 100%;
    }
    #legendbar {
      &>span {
        background: #08304a;
        border: 1px solid #93c2e6;
        opacity: .8;
        color: #00e5ff;
        font-size: 16px;
        line-height: 30px;
        text-align: center;
        letter-spacing: 20px;
        padding-left: 15px;
        height: 30px;
        display: block;
      }
      .legend_box {
        background: rgba(1,51,102,.85);
        border: 1px solid rgba(147,194,230,.8);
        border-top: none;
        padding: 10px 0;
        color: #fff;
      }
    }
  }
</style>
